import { mapState, mapActions } from 'vuex'
import { upyunUrl } from '../../api/config'

export default {
  data () {
    return {
      upyunUrl
    }
  },
  computed: {
    ...mapState([
      'userInfo'
    ])
  },
  methods: {
    ...mapActions([
      'setUserInfo'
    ]),
    onLoginFn () {
      const _this = this
      uni.login({
        provider: 'weixin',
        success (res) {
          _this.setUserInfo(res)
        }
      })
    },
    getUserInfo (e) {
      this.setUserInfo(e)
    },
    onSkipLoginFn () {
      uni.navigateTo({
        url: `/pages/login/agreement/index`
      })
    },
    onSkipReturnFn () {
      if (this.show) {
        uni.navigateBack()
      } else {
        uni.switchTab({
          url: `/pages/index/index`
        })
      }
    }
  },
  props: {
    type: Boolean,
    show: false
  }
}
